	module.exports = {"data":{"postsJson":{"title":"예수님께서 예루살렘으로 들어가셨다","questionsTitle":"질문들, 질문들, 질문들","display":"block","bookName":"누가복음","ref":"19:28-21:38","html":"<p>   <strong>19</strong><u>28</u>예수께서 이 말씀을 하시고 예루살렘을 향하여 앞서서 가시더라 <u>29</u>감람원이라 불리는 산쪽에 있는 벳바게와 베다니에 가까이 가셨을 때에 제자 중 둘을 보내시며 <u>30</u>이르시되</p><h1>예수님</h1><h2>너희는 맞은편 마을로 가라 그리로 들어가면 아직 아무도 타 보지 않은 나귀 새끼가 매여 있는 것을 보리니 풀어 끌고 오라 <u>31</u>만일 누가 너희에게 어찌하여 푸느냐 묻거든 말하기를 주가 쓰시겠다 하라</h2><p>하시매 <u>32</u>보내심을 받은 자들이 가서 그 말씀하신 대로 만난지라<u>33</u>나귀 새끼를 풀 때에 그 임자들이 이르되</p><h1>당나귀 주인</h1><h4>어찌하여 나귀 새끼를 푸느냐</h4><p><u>34</u>대답하되</p><h1>제자들</h1><h3>주께서쓰시겠다</h3><p>하고 <u>35</u>그것을 예수께로 끌고 와서 자기들의 겉옷을 나귀 새끼 위에 걸쳐 놓고 예수를 태우니 <u>36</u>가실 때에 그들이 자기의 겉옷을 길에 펴더라<br />   <u>37</u>이미 감람 산 내리막길에 가까이 오시매 제자의 온 무리가 자기들이 본 바 모든 능한 일로 인하여 기뻐하며 큰 소리로 하나님을 찬양하여 <u>38</u>이르되</p><h1>군중들</h1><h4>찬송하리로다 주의 이름으로 오시는 왕이여<br />   하늘에는 평화요 가장 높은 곳에는 영광이로다</h4><p>하니 <u>39</u>무리 중 어떤 바리새인들이 말하되</p><h1>바리새인들</h1><h4>선생이여 당신의 제자들을 책망하소서</h4><p>하거늘 <u>40</u>대답하여 이르시되</p><h1>예수님</h1><h2>내가 너희에게 말하노니 만일 이 사람들이 침묵하면 돌들이 소리 지르리라</h2><p>하시니라<br />   <u>41</u>가까이 오사 성을 보시고 우시며 <u>42</u>이르시되</p><h1>예수님</h1><h2>너도 오늘 평화에 관한 일을 알았더라면 좋을 뻔하였거니와 지금 네 눈에 숨겨졌도다 <u>43</u>날이 이를지라 네 원수들이 토둔을 쌓고 너를 둘러 사면으로 가두고 <u>44</u>또 너와 및 그 가운데 있는 네 자식들을 땅에 메어치며 돌 하나도 돌 위에 남기지 아니하리니 이는 네가 보살핌 받는 날을 알지 못함을 인함이니라</h2><p><u>45</u>성전에 들어가사 장사하는 자들을 내쫓으시며 <u>46</u>그들에게 이르시되</p><h1>예수님</h1><h2>기록된 바<br />   내 집은 기도하는 집이 되리라<br />   하였거늘 너희는 강도의 소굴을 만들었도다</h2><p>하시니라<br />   <u>47</u>예수께서 날마다 성전에서 가르치시니 대제사장들과 서기관들과 백성의 지도자들이 그를 죽이려고 꾀하되 <u>48</u>백성이 다 그에게 귀를 기울여 들으므로 어찌할 방도를 찾지 못하였더라<br />   <strong>20</strong><u>1</u>하루는 예수께서 성전에서 백성을 가르치시며 복음을 전하실새 대제사장들과 서기관들이 장로들과 함께 가까이 와서 <u>2</u>말하여 이르되</p><h1>유대 지도자들</h1><h4>당신이 무슨 권위로 이런 일을 하는지 이 권위를 준 이가 누구인지 우리에게 말하라</h4><p><u>3</u>대답하여 이르시되</p><h1>예수님</h1><h2>나도 한 말을 너희에게 물으리니 내게 말하라 <u>4</u>요한의 세례가 하늘로부터냐 사람으로부터냐</h2><p><u>5</u>그들이 서로 의논하여 이르되</p><h1>유대 지도자들</h1><h4>만일 하늘로부터라 하면 어찌하여 그를 믿지 아니하였느냐 할 것이요 <u>6</u>만일 사람으로부터라 하면 백성이 요한을 선지자로 인정하니 그들이 다 우리를 돌로 칠 것이라</h4><p>하고 <u>7</u>대답하되</p><h1>유대 지도자들</h1><h4>어디로부터인지 알지 못하노라</h4><p>하니 <u>8</u>예수께서 이르시되</p><h1>예수님</h1><h2>나도 무슨 권위로 이런 일을 하는지 너희에게 이르지 아니하리라</h2><p>하시니라<br />   <u>9</u>그가 또 이 비유로 백성에게 말씀하시기 시작하시니라</p><h1>예수님</h1><h2>한 사람이 포도원을 만들어 농부들에게 세로 주고 타국에 가서 오래 있다가 <u>10</u>때가 이르매 포도원 소출 얼마를 바치게 하려고 한 종을 농부들에게 보내니 농부들이 종을 몹시 때리고 거저 보내었거늘 <u>11</u>다시 다른 종을 보내니 그도 몹시 때리고 능욕하고 거저 보내었거늘 <u>12</u>다시 세 번째 종을 보내니 이 종도 상하게 하고 내쫓은지라<br />   <u>13</u>포도원 주인이 이르되 어찌할까 내 사랑하는 아들을 보내리니 그들이 혹 그는 존대하리라 하였더니 <u>14</u>농부들이 그를 보고 서로 의논하여 이르되 이는 상속자니 죽이고 그 유산을 우리의 것으로 만들자 하고 <u>15</u>포도원 밖에 내쫓아 죽였느니라<br />   그런즉 포도원 주인이 이 사람들을 어떻게 하겠느냐 <u>16</u>와서 그 농부들을 진멸하고 포도원을 다른 사람들에게 주리라<br />   하시니 사람들이 듣고 이르되</h2><h1>군중들</h1><h4>그렇게 되지 말아지이다</h4><p>하거늘<u>17</u>그들을 보시며 이르시되</p><h1>예수님</h1><h2>그러면 기록된 바<br />   건축자들의 버린 돌이<br />   모퉁이의 머릿돌이 되었느니라<br />   함이 어찜이냐 <u>18</u>무릇 이 돌 위에 떨어지는 자는 깨어지겠고 이 돌이 사람 위에 떨어지면 그를 가루로 만들어 흩으리라</h2><p>하시니라<br />   <u>19</u>서기관들과 대제사장들이 예수의 이 비유는 자기들을 가리켜 말씀하심인 줄 알고 즉시 잡고자 하되 백성을 두려워하더라<br />   <u>20</u>이에 그들이 엿보다가 예수를 총독의 다스림과 권세 아래에 넘기려 하여 정탐들을 보내어 그들로 스스로 의인인 체하며 예수의 말을 책잡게 하니 <u>21</u>그들이 물어 이르되</p><h1>서기관들과 대 제사장들</h1><h4>선생님이여 우리가 아노니 당신은 바로 말씀하시고 가르치시며 사람을 외모로 취하지 아니하시고 오직 진리로써 하나님의 도를 가르치시나이다 <u>22</u>우리가 가이사에게 세를 바치는 것이 옳으니이까 옳지 않으니이까</h4><p>하니 <u>23</u>예수께서 그 간계를 아시고 이르시되</p><h1>예수</h1><h2>24데나리온 하나를 내게 보이라 누구의 형상과 글이 여기 있느냐</h2><p>대답하되</p><h1>서기관들과 대 제사장들</h1><h4>가이사의 것이니이다</h4><p><u>25</u>이르시되</p><h1>예수님</h1><h2>그런즉 가이사의 것은 가이사에게, 하나님의 것은 하나님께 바치라</h2><p>하시니 <u>26</u>그들이 백성 앞에서 그의 말을 능히 책잡지 못하고 그의 대답을 놀랍게 여겨 침묵하니라<br />   <u>27</u>부활이 없다고 주장하는 사두개인 중 어떤 이들이 와서<u>28</u>물어 이르되</p><h1>사두개인들</h1><h4>선생님이여 모세가 우리에게 써 주기를 만일 어떤 사람의 형이 아내를 두고 자식이 없이 죽으면 그 동생이 그 아내를 취하여 형을 위하여 상속자를 세울지니라 하였나이다 <u>29</u>그런데 칠 형제가 있었는데 맏이가 아내를 취하였다가 자식이 없이 죽고 <u>30</u>그 둘째와 셋째가 그를 취하고 <u>31</u>일곱이 다 그와 같이 자식이 없이 죽고 <u>32</u>그 후에 여자도 죽었나이다 <u>33</u>일곱이 다 그를 아내로 취하였으니 부활 때에 그 중에 누구의 아내가 되리이까</h4><p><u>34</u>예수께서 이르시되</p><h1>예수님</h1><h2>이 세상의 자녀들은 장가도 가고 시집도 가되 <u>35</u>저 세상과 및 죽은 자 가운데서 부활함을 얻기에 합당히 여김을 받은 자들은 장가 가고 시집 가는 일이 없으며 <u>36</u>그들은 다시 죽을 수도 없나니 이는 천사와 동등이요 부활의 자녀로서 하나님의 자녀임이라<br />   <u>37</u>죽은 자가 살아난다는 것은 모세도 가시나무 떨기에 관한 글에서 주를 아브라함의 하나님이요 이삭의 하나님이요 야곱의 하나님이시라 칭하였나니 <u>38</u>하나님은 죽은 자의 하나님이 아니요 살아 있는 자의 하나님이시라 하나님에게는 모든 사람이 살았느니라</h2><p>하시니 <u>39</u>서기관 중 어떤 이들이 말하되</p><h1>서기관들</h1><h4>선생님 잘 말씀하셨나이다</h4><p>하니 <u>40</u>그들은 아무 것도 감히 더 물을 수 없음이더라<br />   <u>41</u>예수께서 그들에게 이르시되</p><h1>예수님</h1><h2>사람들이 어찌하여 그리스도를 다윗의 자손이라 하느냐 <u>42</u>시편에 다윗이 친히 말하였으되<br />   주께서 내 주께 이르시되<br />   <u>43</u>내가 네 원수를 네 발등상으로 삼을 때까지<br />   내 우편에 앉았으라 하셨도다<br />   하였느니라 <u>44</u>그런즉 다윗이 그리스도를 주라 칭하였으니 어찌 그의 자손이 되겠느냐</h2><p>하시니라<br />   <u>45</u>모든 백성이 들을 때에 예수께서 그 제자들에게 이르시되</p><h1>예수님</h1><h2>46긴 옷을 입고 다니는 것을 원하며 시장에서 문안 받는 것과 회당의 높은 자리와 잔치의 윗자리를 좋아하는 서기관들을 삼가라 <u>47</u>그들은 과부의 가산을 삼키며 외식으로 길게 기도하니 그들이 더 엄중한 심판을 받으리라</h2><p>하시니라<br />   <strong>21</strong><u>1</u>예수께서 눈을 들어 부자들이 헌금함에 헌금 넣는 것을 보시고 <u>2</u>또 어떤 가난한 과부가 두 렙돈 넣는 것을 보시고 <u>3</u>이르시되</p><h1>예수님</h1><h2>내가 참으로 너희에게 말하노니 이 가난한 과부가 다른 모든 사람보다 많이 넣었도다 <u>4</u>저들은 그 풍족한 중에서 헌금을 넣었거니와 이 과부는 그 가난한 중에서 자기가 가지고 있는 생활비 전부를 넣었느니라</h2><p>하시니라<br />   <u>5</u>어떤 사람들이 성전을 가리켜 그 아름다운 돌과 헌물로 꾸민 것을 말하매 예수께서 이르시되</p><h1>예수님</h1><h2>6너희 보는 이것들이 날이 이르면 돌 하나도 돌 위에 남지 않고 다 무너뜨려지리라</h2><p><u>7</u>그들이 물어 이르되</p><h1>네명의 제자들</h1><h3>선생님이여그러면어느때에이런일이있겠사오며이런일이일어나려할때에무슨징조가있사오리이까</h3><p><u>8</u>이르시되</p><h1>예수님</h1><h2>미혹을 받지 않도록 주의하라 많은 사람이 내 이름으로 와서 이르되 내가 그라 하며 때가 가까이 왔다 하겠으나 그들을 따르지 말라 <u>9</u>난리와 소요의 소문을 들을 때에 두려워하지 말라 이 일이 먼저 있어야 하되 끝은 곧 되지 아니하리라</h2><p> <u>10</u>또 이르시되</p><h1>예수님</h1><h2>민족이 민족을, 나라가 나라를 대적하여 일어나겠고 <u>11</u>곳곳에 큰 지진과 기근과 전염병이 있겠고 또 무서운 일과 하늘로부터 큰 징조들이 있으리라<br />   <u>12</u>이 모든 일 전에 내 이름으로 말미암아 너희에게 손을 대어 박해하며 회당과 옥에 넘겨 주며 임금들과 집권자들 앞에 끌어 가려니와 <u>13</u>이 일이 도리어 너희에게 증거가 되리라 <u>14</u>그러므로 너희는 변명할 것을 미리 궁리하지 않도록 명심하라 <u>15</u>내가 너희의 모든 대적이 능히 대항하거나 변박할 수 없는 구변과 지혜를 너희에게 주리라 <u>16</u>심지어 부모와 형제와 친척과 벗이 너희를 넘겨 주어 너희 중의 몇을 죽이게 하겠고 <u>17</u>또 너희가 내 이름으로 말미암아 모든 사람에게 미움을 받을 것이나 <u>18</u>너희 머리털 하나도 상하지 아니하리라 <u>19</u>너희의 인내로 너희 영혼을 얻으리라<br />   <u>20</u>너희가 예루살렘이 군대들에게 에워싸이는 것을 보거든 그 멸망이 가까운 줄을 알라 <u>21</u>그 때에 유대에 있는 자들은 산으로 도망갈 것이며 성내에 있는 자들은 나갈 것이며 촌에 있는 자들은 그리로 들어가지 말지어다 <u>22</u>이 날들은 기록된 모든 것을 이루는 징벌의 날이니라 <u>23</u>그 날에는 아이 밴 자들과 젖먹이는 자들에게 화가 있으리니 이는 땅에 큰 환난과 이 백성에게 진노가 있겠음이로다 <u>24</u>그들이 칼날에 죽임을 당하며 모든 이방에 사로잡혀 가겠고 예루살렘은 이방인의 때가 차기까지 이방인들에게 밟히리라<br />   <u>25</u>일월 성신에는 징조가 있겠고 땅에서는 민족들이 바다와 파도의 성난 소리로 인하여 혼란한 중에 곤고하리라 <u>26</u>사람들이 세상에 임할 일을 생각하고 무서워하므로 기절하리니 이는 하늘의 권능들이 흔들리겠음이라 <u>27</u>그 때에 사람들이 인자가 구름을 타고 능력과 큰 영광으로 오는 것을 보리라 <u>28</u>이런 일이 되기를 시작하거든 일어나 머리를 들라 너희 속량이 가까웠느니라</h2><p>하시더라<br />   <u>29</u>이에 비유로 이르시되</p><h1>예수님</h1><h2>무화과나무와 모든 나무를 보라 <u>30</u>싹이 나면 너희가 보고 여름이 가까운 줄을 자연히 아나니 <u>31</u>이와 같이 너희가 이런 일이 일어나는 것을 보거든 하나님의 나라가 가까이 온 줄을 알라 <u>32</u>내가 진실로 너희에게 말하노니 이 세대가 지나가기 전에 모든 일이 다 이루어지리라 <u>33</u>천지는 없어지겠으나 내 말은 없어지지 아니하리라<br />   <u>34</u>너희는 스스로 조심하라 그렇지 않으면 방탕함과 술취함과 생활의 염려로 마음이 둔하여지고 뜻밖에 그 날이 덫과 같이 너희에게 임하리라 <u>35</u>이 날은 온 지구상에 거하는 모든 사람에게 임하리라 <u>36</u>이러므로 너희는 장차 올 이 모든 일을 능히 피하고 인자 앞에 서도록 항상 기도하며 깨어 있으라</h2><p>하시니라<br />   <u>37</u>예수께서 낮에는 성전에서 가르치시고 밤에는 나가 감람원이라 하는 산에서 쉬시니 <u>38</u>모든 백성이 그 말씀을 들으려고 이른 아침에 성전에 나아가더라</p>","path":"/336","Question1":"이 본문에서 질문하는 모든 질문들을 열거하십시오. 누가 이 질문들을 하고 있습니까? 누구에게 하고 있는 것입니까?","Question2":"당신의 눈에 띄는 질문이 있습니까?","Question3":"이 본문의 저자의 어떤 질문에 대답하기 원합니까?","Question4":"이 본문이 하는 이 질문들이 당신의 삶을 위해 어떤 함축된 의미를 가지고 있습니까? ","prevPath":"/335","nextPath":"/337"}},"pathContext":{}}

